<template>
    <div>
        <loader :is-visible="isLoading"></loader>

        <div id="divMainFormData">
            <LeftMenu :userid="loggeduserid" :SharedFolder="SharedFolderParent" :sharedChildFolder="sharedChildFolder"
                :RecentPinnedFolderArray="RecentFolderArray" :organizationSpace="organizationSpace"
                :pinnedFolderArray="pinnedFolderArray" :UserList="SharedUserList" :FolderArray="FindalFolderArray"
                @FilesListing="FilesListing"></LeftMenu>
        </div>
        <section id="main-content" class="main-content">
            <section class="main-content">
                <div class="right-content hr_jobpost" id="rightMenuRecycle">
                    <div class="col-md-12 p-0 mb-2">
                        <div class="theme-primary partition p-actions-expand">
                            <span class="p-name text-white">{{ FolderName }}</span>
                            <span class="p-actions float-right">
                                <a title="" id="backtolist" @click="BackToList"
                                    class="p-action-btn text-white float-right" :data-original-title="$t('BacktoList')">
                                    <em class="fa fa-angle-double-left pr-2"></em><br />{{ $t('BacktoList') }}
                                </a>
                                <a class="p-action-btn text-white float-right"
                                    @click="DownloadDocFolder(FolderName)"><i class="fa fa-download"></i><br />{{
                                        $t('Download') }}</a>
                                <a class="p-action-btn text-white float-right" :class="permission == 'r'|| permission == 'w' ? 'disabled' : ''"
                                    @click="CommonDelete"><i class="fa fa-trash"></i><br />{{ $t('Delete') }}</a>

                                <a class="p-action-btn text-white float-right" :class="permission == 'r'|| permission == 'w' ? 'disabled' : ''"
                                    v-on:click="SharePopup"><em class="fa fa-share-alt"></em><br />{{ $t('Share') }}</a>
                                <a class="p-action-btn text-white float-right"
                                    :class="permission == 'r' || isGenerateLink == 1 ||  permission == 'w'? 'disabled' : ''"
                                    @click="GeneratePublickLink"><i class="fa fa-link"
                                        :class="permission == 'r' ? 'disabled' : ''"></i><br />{{ $t('Generatepubliclink')
                                        }}</a>
                                <a class="p-action-btn text-white float-right" @click="CopyFileFolder(FolderData.FileId)"
                                    :class="permission == 'r' ? 'disabled' : ''"><em class="fa fa-files-o"
                                        aria-hidden="true"></em><br />{{ $t('Copy') }}</a>
                                <a class="p-action-btn text-white float-right" :class="permission == 'r'  || permission == 'w'? 'disabled' : ''"
                                    @click="MoveFolderFiles" :id="FileId"><em class="fa fa-arrows"></em><br />{{ $t('Move')
                                    }}</a>
                            </span>
                            <span class="collapse-head-btns">
                                <a href="javascript:;" class="collapse-btns">
                                    <em class="fa fa-angle-up text-white"></em>
                                </a>
                            </span>
                        </div>
                    </div>
                    <div class="tab-pane mb-0">
                        <div class="tab-content fsmtabcontrolMobileView" id="myTabContent">
                            <div class="expand-list">
                                <a href="javascript:;"  @click="ExpandCollapseList" class="expand-list-btn justify-content-center align-items-center">
                                    <em class="icon-solid arrow-solid-left text-white" style="font-size:9pt"></em>
                                </a>
                            </div>
                            <div class="tab-pane p-0 pb-2 border-0 active" role="tabpanel" id="tab-detail-content"
                                aria-labelledby="tab-detail-content">
                                <div class="listing">
                                    <div class="row">
                                        <div class="col-md-12">
                                            <div class="card mb-3 probx">
                                                <div class="card-header">
                                                    <h2>{{ $t('Details') }}</h2>
                                                    <div class="cont_right">
                                                        <div class="bar"></div>
                                                    </div>
                                                </div>
                                                <div class="card-body" v-for="item in FolderData">
                                                    <div class="panel1 row">
                                                        <div class="col-12 p-0">
                                                            <div
                                                                class="col-sm-12 col-md-6 col-lg-2 col-xl-2 default-tag-vissible">
                                                                <div class="box border positionrelative float-left w-100">
                                                                    <div class="img-ico text-center p-2">
                                                                        <em
                                                                            class="fa fa-folder pr-2 font-128 text-primary"></em>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                            <div class="row col-sm-12 col-md-6 col-lg-10 col-xl-10">
                                                                <div class=" col-sm-12 col-md-6 col-lg-6 col-xl-4">
                                                                    <div class="form-group"><label><b>{{ $t('FolderName')
                                                                    }}: </b>
                                                                            <span class="answer rightlabeltext">{{
                                                                                item.storage_folder_name
                                                                            }}</span>
                                                                        </label></div>
                                                                </div>

                                                                <div class=" col-sm-12 col-md-6 col-lg-6 col-xl-4">
                                                                    <div class="form-group"><label><b>{{
                                                                        $t('LastModifiedBy') }}:
                                                                            </b><span class="answer rightlabeltext">{{
                                                                                item.modified_by
                                                                            }}</span>
                                                                        </label></div>
                                                                </div>


                                                                <div class=" col-sm-12 col-md-6 col-lg-6 col-xl-4">
                                                                    <div class="form-group"><label><b>{{ $t('FolderSize')
                                                                    }}:
                                                                            </b>
                                                                            <span class="answer rightlabeltext">{{
                                                                                convertmbtoKb(item.storage_folder_size)
                                                                            }}</span>

                                                                        </label></div>
                                                                </div>
                                                                <div class=" col-sm-12 col-md-6 col-lg-6 col-xl-4">
                                                                    <div class="form-group"><label><b>{{ $t('CreatedOn') }}:
                                                                            </b><span class="answer rightlabeltext">{{
                                                                                $options.filters.formatDate(item.created_at)
                                                                            }}</span>
                                                                        </label></div>
                                                                </div>
                                                                <div class=" col-sm-12 col-md-6 col-lg-6 col-xl-4">
                                                                    <div class="form-group" data-v-f8df6780="">
                                                                        <label data-v-f8df6780=""><b data-v-f8df6780="">{{
                                                                            $t('SharedWith') }}: </b>
                                                                            <a href="#" class="mr-3 ml-2"
                                                                                data-toggle-tooltip="tooltip"
                                                                                data-original-title="Associate Contact"
                                                                                @click="showSharedDetilsModel(item.storage_folder_id, item.StorageUniqueCode)">
                                                                                <span class="icon-1 single-action">
                                                                                    <em class="fa fa-folder-open text-info"
                                                                                        data-original-title="xdfgs"></em>
                                                                                </span>
                                                                            </a>
                                                                            <a href="#" title="Users" class="mr-3"
                                                                                data-toggle-tooltip="tooltip"
                                                                                data-placement="bottom"
                                                                                @click="ShowUsersListing(item.storage_folder_id, item.StorageUniqueCode)">
                                                                                <span class="icon-1 single-action">
                                                                                    <em class="fa fa-user text-info"></em>
                                                                                </span>
                                                                            </a>
                                                                            <a href="#" title="Group" class="mr-3"
                                                                                data-toggle-tooltip="tooltip"
                                                                                data-placement="bottom"
                                                                                @click="OpenGroupListing(item.storage_folder_id, item.StorageUniqueCode)">
                                                                                <span class="icon-1 single-action">
                                                                                    <em class="fa fa-users  text-info"></em>

                                                                                </span>
                                                                            </a>
                                                                            <a href="#" title="Department" class="mr-3"
                                                                                data-toggle-tooltip="tooltip"
                                                                                data-placement="bottom"
                                                                                @click="ShowDepartmentListing(item.storage_folder_id, item.StorageUniqueCode)">
                                                                                <span class="icon-1 single-action">
                                                                                    <em
                                                                                        class="fa fa-vcard-o text-info"></em>
                                                                                </span>
                                                                            </a>
                                                                        </label>
                                                                    </div>
                                                                </div>

                                                                <div class=" col-sm-12 col-md-6 col-lg-6 col-xl-4">
                                                                    <div class="form-group"><label><b>{{ $t('CreatedBy') }}:
                                                                            </b><span class="answer rightlabeltext">{{
                                                                                item.created_by
                                                                            }}</span>
                                                                        </label></div>
                                                                </div>

                                                                <div class=" col-sm-12 col-md-6 col-lg-6 col-xl-4">
                                                                    <div class="form-group"><label><b>Copy Link:
                                                                           </b><span class="answer rightlabeltext"><a
                                                                                    style="color:#55a9e1;"
                                                                                    v-if="item.IsGenerateLink == 1"
                                                                                    @click="CopyLink(item.GeneratedLink)"
                                                                                    href="#">{{ $t('ClicktoCopy') }}<em
                                                                                        style="color: #0095ff;"
                                                                                        class="fa fa-clone"></em></a>
                                                                            </span>
                                                                            <span class="answer rightlabeltext disabled"><a
                                                                                    style="color:#55a9e1;" href="#"
                                                                                    class="text-danger"
                                                                                    v-if="item.IsGenerateLink == 0 || item.IsGenerateLink == null">{{
                                                                                        $t('NotAvailabe') }}
                                                                                </a></span>
                                                                        </label>
                                                                        <span v-show="tool" class="tooltiptext">{{
                                                                            text
                                                                        }}</span>


                                                                    </div>
                                                                </div>


                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </section>
        </section>
        <ModalSharePopup :idFile="idFile" :folderId="folderId" :docCode="docCode" :StorageUniqueCode="StorageUniqueCode"
            :generateJSONData=generateJSONData :Name="Name" v-if="sharemodal == true"
            @closePopupGroupListing="closePopupGroupListing"></ModalSharePopup>
        <ModalMoveFolderFiles :FolderArray="FindalFolderArray" @MoveFoldersFiles="MoveFoldersFiles"
            @CloneFoldersFiles="CloneFoldersFiles" :checkActionType="checkActionType"
            @closemovefilefolderpopup="closemovefilefolderpopup" @CopyFileFolder="CopyFileFolder"
            @FolderIdmove="FolderIdmove" v-if="showmovefolderpopup == true"></ModalMoveFolderFiles>
        <ModalAddGroup @ModalAddGroups="ModalAddGroups" @CloseAddGroupPopup="CloseAddGroupPopup" v-if="ShowModalAddGroup">
        </ModalAddGroup>
        <ModelGroupListing :generateJSONData=generateJSONData :docCode="docCode" :folderId="folderId" @ModalGroupListing="ModalGroupListing"
            :StorageUniqueCode="StorageUniqueCode" @FetchEditGroups="FetchEditGroups"
            @CloseAddGroupPopup="CloseAddGroupPopup" @CloseGroupListingPopup="CloseGroupListingPopup"
            :ModalLabelForlsListing="ModalLabelForlsListing" :FileId='FileId' :refCode='refCode' v-if="ShowGruopListing">
        </ModelGroupListing>
        <CopyConfirmation v-if="copyFileInfo" :copyFileInfo="copyFileInfo" @SaveCopyFile="SaveCopyFile"></CopyConfirmation>
    </div>
</template>
<script>

import LeftMenu from "@/components/LeftMenu.vue"
import DataService from "../../services/DataService";
import doctimeline from "../../components/Common/Modal/DocTimeLine.vue";
import ModalMoveFolderFiles from "./Modal/ModalMoveFolderFiles.vue"
import ModalSharePopup from "@/views/UI/ShareDocument.vue"
import ModalAddGroup from "./Modal/ModalAddGroup.vue"
import ModelGroupListing from "./Modal/ModalGruopListing.vue"
import VueEasyLightbox from 'vue-easy-lightbox'
const docstorage = process.env.VITE_DOCSTORAGE_API;
import CopyConfirmation from './Modal/CopyConfirmation.vue';
export default {
    components: {
        LeftMenu,
        doctimeline,
        VueEasyLightbox,
        ModalMoveFolderFiles,
        ModalSharePopup,
        docstorage,
        ModalAddGroup,
        ModelGroupListing,
        CopyConfirmation
    },
    data() {
        return {
            isLoading: false,
            loggeduserid: null,
            SharedFolderParent: [],
            FindalFolderArray: [],
            folderTreeList: [],
            sharedFolder: [],
            abc: [],
            FolderData: [],
            SharedType: "",
            images: '',
            CreatedOn: "",
            ishow: false,
            isShared: "",
            FolderName: "",
            visibleRef: false,
            ShowGruopListing: false,
            showmovefolderpopup: false,
            folderFileId: null,
            ModalLabelForlsListing: '',
            refCode: '',
            FileId: '',
            docCode: null,
            fileTimelineData: [],
            sharemodal: false,
            ShowModalAddGroup: false,
            url: "",
            fileType: "",
            showFilesOldVersion: false,
            filesOldVersionListing: [],
            files: '',
            folderId: '',
            StorageUniqueCode: '',
            recentFolderList: [],
            pinnedFolderList: [],
            RecentFolderArray: [],
            pinnedFolderArray: [],
            organizationSpace: [],
            tool: false,
            text: "Copied!",
            permission: null,
            checkActionType: '',
            isGenerateLink: null,
            GroupListingData: [],
            generateJSONData: [],
            isGenerateLink: null,
            sharedparentfolder: [],
            sharedChildFolder: [],
            folderUniqueCode: null,
            copyFileInfo: null,
            commonParentName: "",
        };
    },
    created: function () {
        var pathname = window.location.pathname;
        this.loggeduserid = this.GetUserInfo.ID;
        this.FileId = pathname.split("/")[2];
        this.permission = this.$route.params.Permission;
        this.commonParentName = this.$route.params.CommonParentName
        this.GetFullTree();
        if (pathname.split("/")[2]) {
            this.GetStorageFolderDetail(pathname.split("/")[2]);
        };
        this.folderId = pathname.split("/")[2]
        this.GetSharedFolder();
    },

    methods: {

        convertmbtoKb: function (val) {
            var convertKb = 0;
            if (val == 0) {
                convertKb = '0 Bytes'
            } else
                if (val < 1) {
                    convertKb = val.toFixed(2) + ' KB'
                }
                else {
                    convertKb = val.toFixed(2) + ' MB'
                }
            return convertKb;
        },
        /*
        Listing method for shared folders with project. 
        */
        showSharedDetilsModel(storage_folder_id, StorageUniqueCode) {

            var vm = this;
            vm.isLoading = true;
            vm.ModalLabelForlsListing = vm.$t('ProjectListing');
            vm.refCode = 'Project';
            vm.FileId = storage_folder_id;
            vm.StorageUniqueCode = StorageUniqueCode;
            vm.ShowGruopListing = true;
            vm.isShow = false
            vm.isLoading = false;
        },
        /*
       Listing method for shared folders with users. 
       */
        ShowUsersListing: function (storage_folder_id, StorageUniqueCode) {

            var vm = this;
            vm.ModalLabelForlsListing = vm.$t('UserListing');
            vm.refCode = 'Users';
            vm.FileId = storage_folder_id;
            vm.StorageUniqueCode = StorageUniqueCode;
            vm.ShowGruopListing = true;
        },
        /*
       Listing method for shared folders with department. 
       */
        ShowDepartmentListing: function (storage_folder_id, StorageUniqueCode) {

            var vm = this;
            vm.ModalLabelForlsListing = vm.$t('DepartmentListing');
            vm.refCode = 'Department';
            this.FileId = storage_folder_id;
            vm.StorageUniqueCode = StorageUniqueCode;
            vm.ShowGruopListing = true;
        },
        /*
       Listing method for shared folders with group. 
       */
        OpenGroupListing: function (storage_folder_id, StorageUniqueCode) {

            var vm = this;
            vm.ModalLabelForlsListing = vm.$t('GroupListing');
            vm.refCode = 'UserGroups';
            vm.FileId = storage_folder_id;
            vm.StorageUniqueCode = StorageUniqueCode;
            vm.ShowGruopListing = true;
        },
        CloseGroupListingPopup: function () {
            this.ShowGruopListing = false;
        },
        /*
        open popup for share document
        */
        SharePopup: function () {
            var vm = this;
            vm.sharemodal = true;
            vm.idFile = vm.FolderData[0].storage_folder_id;
            vm.docCode = 0;
            vm.StorageUniqueCode = vm.FolderData[0].storage_unique_code;
        },
        DownloadDocFolder: function (FolderName) {
            var vm = this;
            var params = vm.folderId;
            var paths = ""
            DataService.DownloadDocFolder(params).then(function (response) {
                var path = "";
                var fileURL = window.URL.createObjectURL(new Blob([response.data]));
                var fileLink = document.createElement('a');
                fileLink.href = fileURL;
                fileLink.setAttribute('download', FolderName.toString() + ".zip");
                document.body.appendChild(fileLink);
                fileLink.click();
                //setInterval(function () { vm.DeleteTempDownloadDocFolder(path) }, 1000);
                //window.location.replace(path);


            })

        },

        DeleteTempDownloadDocFolder: function (params) {
            DataService.DeleteTempDownloadDocFolder(params).then(function (response) {
            })
        },




        closemovefilefolderpopup: function () {
            var vm = this;
            vm.showmovefolderpopup = false;
        },
        closePopupGroupListing: function () {
            this.sharemodal = false;
        },
        closepopup: function () {
            this.sharemodal = false;
        },
        ModalAddGroup: function () {
            this.ShowModalAddGroup = true;
        },
        CloseAddGroupPopup: function () {
            this.ShowModalAddGroup = false;
        },
        FolderIdmove: function (fid, parentid) {            
            var vm = this;
            vm.folderId = fid;
        },
        CopyFileFolder: function (id, docCode) {            
            var vm = this;
            vm.confirmR(vm.$t("Are you sure you want to Copy these file(s)."), true, (vm.$t("Copy") + "  " + vm.$t("Folder")), function () {
                vm.checkActionType = 'Copy';
                vm.showmovefolderpopup = true;
                vm.folderFileId = id;
                vm.doccode = 0;
            })
        },
        MoveFoldersFiles: function () {            
            var vm = this;
            let obj = {
                SourceId: vm.FileId,
                RefCode: 0
            }
            var jsonstr = JSON.stringify(obj);
            var params = `jsonData=${jsonstr}&destFolderid=${vm.folderId}`;
            DataService.MoveFileFolder(params).then((response) => {
                if (response.data == 1) {
                    vm.ShowAlert(vm.$t("MoveFolderFileSucces"), "success", true, vm.$t("Alert"));
                    vm.StorageDocumentListing();
                    vm.closepopupFolder();
                }
                else {
                    vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                }
            })
        },
        CloneFoldersFiles: function () {
            
            var vm = this;
            var params = `fileIds=${vm.FileId}&destFolderId=${vm.folderId}&opr=Copy`;
            DataService.StartCopyMoveOpr(params).then((response) => {
                console.log(response);
                vm.copyFileInfo = response.data.fileList;
            })
        },
        SaveCopyFile(fileList) {
            
            var vm = this;
            var data = JSON.stringify(fileList);
            DataService.CloneFilesInFolder(data).then(function (response) {

                console.log(response);
                vm.copyFileInfo = null;
                setTimeout(() => {
                    vm.copyFileInfo = response.data;
                }, 100);

            })
        },
        MoveFolderFiles: function () {

            this.showmovefolderpopup = true;
            this.checkActionType = 'Move';
        },
        GetStorageFolderDetail(folderId) {
            debugger;
            var params = { folderId };
            DataService.GetStorageFolderDetail(params).then((response) => {
                debugger
                var StorageFolderSummary = JSON.parse(response.data);
                this.isGenerateLink = JSON.parse(response.data).FolderJsonData[0].IsGenerateLink
                this.folderUniqueCode = JSON.parse(response.data).FolderJsonData[0].storage_unique_code
                this.FolderData = StorageFolderSummary.FolderJsonData;
                this.CreatedOn = this.FolderData[0].created_at;
                this.FolderName = this.FolderData[0].storage_folder_name;
            })
        },
        showTool() {

            this.tool = true
            setTimeout(() => this.tool = false, 1000)
        },
        BackToList: function () {
            debugger
            this.$router.push({ path: "/File/DashBoard", query: { FolderId: this.folderId == 'null' ? '0' : this.folderId, ParentName: this.commonParentName, RefCode: this.$route.params.RefCode, AddClass: "a" + this.folderId, View: this.$route.params.View } });
        },
        CopyLink(GeneratedLink) {
            
            var vm = this;
            var Id = window.location.pathname.split("/")[2];
            var EncryptFileId = btoa(Id);
            var params = 'FolderId=' + EncryptFileId + '&UniqueCode=' + vm.folderUniqueCode + '&FolderName=' + this.FolderName + ''
            var pathname = window.location.origin + '/DownloadPublicFolder?' + params + '';
            vm.UniqueUrl = docstorage + GeneratedLink;
            navigator.clipboard.writeText(pathname);
            vm.showTool();
        },
        GeneratePublickLink: function () {
            
            var vm = this;
            vm.isLoading = true;
            let obj = {
                SourceId: vm.folderId,
                RefCode: 0
            }
            vm.generateJSONData.push(obj);

            var params = `docJsonData=${JSON.stringify(vm.generateJSONData)}`;
            DataService.GenerateDocPublicLink(params).then((response) => {
                
                if (response.data.code == 1) {
                    vm.StorageUniqueCode = response.data.docUniqueCode;
                    vm.ShowAlert("Generate link has been successfully generated");
                    vm.GetStorageFolderDetail(vm.folderId);
                    vm.isLoading = false;
                    return vm.StorageUniqueCode
                }
            });
        },
        GetRecentlyAddPinnedFolder: function () {
            var vm = this;
            DataService.GetRecentlyAndPinnedFolderTreeData().then((response) => {
                vm.recentFolderList = JSON.parse(response.data).RecentFolderList
                vm.pinnedFolderList = JSON.parse(response.data).PinnedFolderList
                vm.recentFolderList.forEach((value, index) => {
                    if (value.OWNER_USER_ID == vm.loggeduserid) {
                        var CheckIfExist = vm.RecentFolderArray.filter(function (el) {
                            return el.FOLDER_ID == value.FOLDER_ID;
                        });
                        if (CheckIfExist.length == 0) {
                            if (value.FOLDER_PARENT_ID == null) {
                                let folderId = value.FOLDER_ID
                                let rslt = vm.getsharedChildFolder(folderId, value, vm.folderTreeList);
                                vm.RecentFolderArray.push(rslt)
                            }
                        }

                    }

                });
                vm.pinnedFolderList.forEach((value, index) => {
                    if (value.OWNER_USER_ID == vm.loggeduserid) {
                        var CheckIfExist = vm.pinnedFolderArray.filter(function (el) {
                            return el.FOLDER_ID == value.FOLDER_ID;
                        });


                        if (CheckIfExist.length == 0) {
                            if (value.FOLDER_PARENT_ID == null) {
                                let folderId = value.FOLDER_ID
                                let rslt = vm.getsharedChildFolder(folderId, value, vm.folderTreeList);
                                vm.pinnedFolderArray.push(rslt)
                            }
                        }

                    }

                });
            });

        },
        GetOrganizationSpace: function () {

            var vm = this;
            DataService.GetOrganizationFolderTreeData().then((response) => {
                response.data.folderTreeList.forEach((value, index) => {
                    var CheckIfExist = vm.organizationSpace.filter(function (el) {
                        return el.FOLDER_ID == value.FOLDER_ID;
                    });


                    if (CheckIfExist.length == 0) {
                        if (value.FOLDER_PARENT_ID == null) {
                            let folderId = value.FOLDER_ID
                            let rslt = vm.getsharedChildFolder(folderId, value, vm.folderTreeList);
                            vm.organizationSpace.push(rslt)
                        }
                    }
                });
            });


        },
        getsharedChildFolder: function (folderId, currentItem, data) {

            let ths = this;
            let retrunArray = currentItem
            var childArray = data.filter(function (el) {
                return el.FOLDER_PARENT_ID == folderId;
            });
            let finalChildArray = [];
            childArray.forEach((value, index) => {
                let ChildfolderId = value.FOLDER_ID
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.FOLDER_ID == value.FOLDER_PARENT_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {

                        return el.FOLDER_PARENT_ID == ChildfolderId;


                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getsharedChildFolder(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        finalChildArray.push(value);
                    }
                }
            });
            retrunArray.chidFolder = finalChildArray;
            return retrunArray
        },
        FilesListing(params) {
            var vm = this;
            vm.folderId = params;
            DataService.FileList(params).then((response) => {
                vm.FilesListingData = response.data.FileList;
                vm.folderSummary = response.data.folderSummary; vm.SharedWith = response.data.folderSummary;
                setTimeout(function () {
                    vm.ResponsiveDataTable('tablelistingdata')
                }, 500);
                setTimeout(function () { vm.CheckBoxBootstrap() }, 100);
            })
        },
        GetSharedFolder: function () {

            var vm = this;
            DataService.GetSharedFolderTree().then((response) => {

                vm.sharedparentfolder = response.data.SharedFolderData;
                vm.sharedparentfolder.forEach((value, index) => {
                    if (value.SHARED_USER_ID == vm.loggeduserid) {
                        var CheckIfExist = vm.sharedChildFolder.filter(function (el) {
                            return el.FOLDER_ID == value.FOLDER_ID;
                        });


                        if (CheckIfExist.length == 0) {
                            if (value.FOLDER_PARENT_ID == null) {
                                let folderId = value.FOLDER_ID
                                let rslt = vm.getsharedChildFolder(folderId, value, vm.folderTreeList);
                                vm.sharedChildFolder.push(rslt)
                            }
                        }

                    }

                });

            });
        },
        getChildFolder: function (folderId, currentItem, data) {
            let ths = this;
            let retrunArray = currentItem
            var childArray = data.filter(function (el) {
                return el.FOLDER_PARENT_ID == folderId;
            });
            let finalChildArray = [];
            childArray.forEach((value, index) => {
                let ChildfolderId = value.FOLDER_ID
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.FOLDER_ID == value.FOLDER_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {
                        return el.FOLDER_PARENT_ID == ChildfolderId;
                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getChildFolder(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        finalChildArray.push(value);
                    }
                }
            });
            retrunArray.chidFolder = finalChildArray;
            return retrunArray
        },
        GetFullTree() {
            var vm = this;
            DataService.FolderFullTreeView().then((response) => {
                response.data.folderTreeList.forEach((value, index) => {
                    if (value.OWNER_USER_ID == this.loggeduserid && value.IsGlobal == 0) {
                        var CheckIfExist = vm.FindalFolderArray.filter(function (el) {
                            return el.FOLDER_ID == value.FOLDER_ID;
                        });

                        if (CheckIfExist.length == 0) {
                            if (value.FOLDER_PARENT_ID == null) {
                                let folderId = value.FOLDER_ID
                                let rslt = vm.getChildFolder(folderId, value, response.data.folderTreeList)
                                vm.FindalFolderArray.push(rslt)
                                vm.GetRecentlyAddPinnedFolder();
                                vm.GetOrganizationSpace();
                            }
                        }
                    }
                });
                vm.folderTreeList = response.data.folderTreeList;
                vm.SharedUserList = response.data.userList;
                vm.sharedFolder = response.data.sharedTreeList;
                vm.folderTreeList.forEach((value, index) => {
                    vm.sharedFolder.forEach((item, index) => {
                        if (item.ParentId != value.FOLDER_ID && value.SHARED_USER_ID == this.loggeduserid) {
                            vm.abc.push(value);
                            vm.SharedFolderParent = [...new Set(vm.abc)]
                        }
                    })
                })
            });
        },
        closepopupFolder: function () {
            this.showmovefolderpopup = false;
        },
        //Delete Files and Folder
        CommonDelete: function (Id, doccode) {            
            var vm = this;
            vm.confirmR(vm.$t("ConfirmDeleteRecord"), true, (vm.$t("Delete") + "  " + vm.$t("Groups")), function () {
                let params = {};
                params["ids"] = vm.folderId;
                params["refCode"] = "Doc_Folder";
                params["companyId"] = vm.loggedcompanyid;
                params["userId"] = vm.loggeduserid;
                params["moduleName"] = "DocStorage";
                DataService.CommonDelete(params).then(function (response) {
                    vm.isLoading = true;
                    var box = "";
                    if (response.data[0].Status == "Success") {
                        $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                        $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                        $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                        vm.$emit("StorageDocumentListing");
                        box = vm.ShowAlert([vm.$t('Folder')], "success", true, vm.$t("Alert"));
                        vm.$router.push({ name: 'DashBoard', });
                    }
                    else {
                        box = box = vm.ShowAlert(vm.$t("DeletedError", [vm.$t('Folder')]), "success", true, vm.$t("Alert"));
                    }
                },);
            })

        },
        //Feth data all listing data for users groups

        FetchData: function () {
            var vm = this;
            vm.GroupListingData = [];
            var params = `CompanyId=${this.companyId}&UserId=${this.loggeduserid}&refCode='Users'&sourceId=${vm.FileId}`;
            DataService.GetUserGroupListing(params).then(function (response) {
                vm.GroupListingData = response.data.DocStorageSharedUserListing;
                if (vm.GroupListingData == null) {
                    vm.NoRecordLength = 0;
                }
            })
            setTimeout(function () { vm.ResponsiveDataTable('tablelistingdata') }, 500);
        },
         ExpandCollapseList() {
            var element1 = document.getElementById("leftmenutree");
            element1.classList.toggle("left-menu-tab-collapsed");
            var element2 = document.getElementById("rightMenuRecycle");
            element2.classList.toggle("right-content-expand");
        },
    }
};
</script>