<template>
    
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block ">
            <div class="modal-dialog modal-dialog-centered ui-draggable">
                <div class="modal-content">
                    <div class="modal-header ui-draggable-handle  py-0">
                        <h4 name="header" class="modal-title mt-2 pt-1">
                          Shared Members List
                        </h4>
                        <div>
                        <a href="javascript:;" id="iconDeleteAll" @click="UsersDelete" title="Delete" class="p-action-btn text-white disabled">
                            <em class="fa fa-trash"></em> <br />Delete
                        </a>
                    </div>
                        <button @click="ClosePopup" class="close"><em aria-hidden="true"
                                class="fa fa-times"></em> </button>
                    </div>
                    <div class="listing py-3 px-3">
                        <div class="table-responsive table-fix-header" id="div_forListView">
                            <table class="table table-bordered mt-0" style="width: 100%;" id="tblUserList">
                                <thead class="thead-bg">
                                    <tr>
                                        <th style="width:50px !important;" class="text-center"><input id="chkAll"  class="chkitems" value="" type="checkbox" v-on:change="onChange()"></th>
                                        <th>
                                            <span>Shared Member List</span>
                                        </th>
                                        
                                        <th data-width="5%" class="text-center nonresize">
                                            <span>Action</span>
                                        </th>
                                    </tr>
                                </thead>
                                <tbody >
                                    <tr v-for="item in SharedData" :key="item">
                                        <td class="text-center"><input :GroupId="item.UserId" v-bind:value="item.UserId"  class="chkItems" type="checkbox" v-on:change="onChange()"></td>
                                        <td v-if="IsShowColumn">
                                            <span class="text-overflow-dynamic-container">
                                                {{ item.UserName }}
                                            </span>
                                        </td>
                                      
                                        <td class="single-action text-center">
                                            <a class="actions-onclick float-none" typetitle="Delete" typefor="Delete"
                                                v-bind:value="item.UserId" title="Delete" @click="UsersDelete(item.UserId)">
                                                <em class="fa fa-trash text-success action-icon"></em> 
                                            </a>
                                        </td>
                                    </tr>
                                    <tr>
                                        <td v-if="SharedData.length==0" colspan="7" class="text-danger text-center no-record">{{$t('NoRecordfound')}}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                    
                </div>
            </div>
        </div>
    </div>
   
</template>


<script>
import SharedListDataService from "@/services/DataService";
export default{
    props: {
        RecordId:
        {
            type:String,
            required:true
        },
        FileId:
        {
            type:String,
            required:true
        },
        refCode:
        {
            type:String,
            required:true
        }
    },
    components: {
      
    },
    data()
    {
        return{  
        isLoading: false,
        IsShowColumn:true,
        SharedData:[],
        ListData:[]
        }

    },
    created: function(){
        this.BindSharedList()
    },
    methods: {
        ClosePopup: function()
        {
            this.$parent.ClosePopup();
            this.$parent.FetchData();
        },

        // Method for bind listing of users
        BindSharedList: function()
        {
            var vm = this;
            vm.isLoading=true;
            var params = `sourceId=${vm.FileId}&parentId=${vm.RecordId}&refCode=${vm.refCode}`;
            SharedListDataService.GetUserSharedListing(params).then(function (response) {
                if(response.data!=null)
                {
                    vm.ListData=JSON.parse(response.data);
                    vm.SharedData=vm.ListData.DocParentUserListing;
                }
                vm.ListData=JSON.parse(response.data);
                vm.SharedData=vm.ListData.DocParentUserListing;
                vm.isLoading=false;
            })
            setTimeout(function(){vm.ResponsiveDataTable('tablelistingdata')}, 500);
            vm.isLoading=false;
            
        },
        onChange: function () {
             var checkCount = $('.chkItems:checked').not("[id^='chkAll']").length;
             if (checkCount > 0) {
                 $('#iconDeleteAll').removeClass('disabled');
             }
             else {
                 $('#iconDeleteAll').addClass('disabled');
             }
         },

        // Method Used for delete user 
        UsersDelete: function(id)
        {
            var vm = this;
            var val = "";
            $('#tblUserList .chkItems:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0) {
                    val += ',';
                }
                val += $(this).val();
            });
            var str = "";
            if (val.length > 0 || id != null) {
                vm.confirmR(vm.$t("ConfirmDeleteRecord"), true, (vm.$t("Delete") + "  " + vm.$t("Members")), function () {
                    vm.isLoading=true;
                    if(id > 0)
                    { 
                        str = id;
                    }
                    else
                    {
                        str += val + ',';
                    }
                    var params= `parentIds=${vm.RecordId}&sharedUserIds=${str}&sourceId=${vm.FileId}&refCode=Shared_Doc_Storage`;
                    SharedListDataService.DeleteParentsUsersSharedFilesFoldersBySource(params).then(function (response) {
                        
                        var box = "";
                       
                            if(response.data==1)
                            {
                                $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                            vm.BindSharedList();
                            box =  vm.ShowAlert(vm.$t("DeletedSuccess",[vm.$t('Users')]),"success", true, vm.$t("Alert"));
                            }
                            else
                            {
                                box =  vm.ShowAlert(vm.$t("DeletedError",[vm.$t('Users')]),"Failure", true, vm.$t("Alert"));
                            }
                        
                    });
                    vm.isLoading=false;
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading=false;
        },
    }
}
</script>