<template>
    <div>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block ">
            <div class="modal-dialog modal-dialog-centered ui-draggable">
                <div class="modal-content">
                    <div class="modal-header ui-draggable-handle  py-0">
                        <h4 name="header" class="modal-title mt-2 pt-1">
                            {{ ModalLabelForlsListing }}
                        </h4>
                        <div>


                            <a id="aAddFolder" href="javascript:;" @click="OpenAddGroupPopup"
                                class="p-action-btn text-white" title="" data-toggle-tooltip="tooltip"
                                v-if="refCode == 'Groups'" data-original-title="Add">
                                <em class="fa fa-plus text-white"></em> <br>Add Group
                            </a>
                            <a href="javascript:;" id="aDeleteAll" @click="CommonDelete" title="Delete"
                                class="p-action-btn text-white disabled">
                                <em class="fa fa-trash"></em> <br />Delete
                            </a>
                        </div>
                        <button @click="ClosePopupGroups" class="close"><em aria-hidden="true"
                                class="fa fa-times"></em> </button>
                    </div>
                    <div class="listing py-3 px-3">
                        <div class="table-responsive table-fix-header" id="div_forListView">
                            <table class="table table-bordered mt-0" style="width: 100%;" id="tblFileList">
                                <thead class="thead-bg">
                                    <tr>
                                        <th style="width:50px !important;" class="text-center"><input id="chkAll"
                                                class="chkitems" value="" type="checkbox" v-on:change="onChange()"></th>
                                        <th>
                                            <span>Name</span>
                                        </th>
                                        <th>
                                            <span>Permissions</span>
                                        </th>
                                        <th v-if="refCode!='Users'">
                                            <span>Shared Member</span>
                                        </th>
                                        <th data-width="5%" class="text-center nonresize">
                                            <span>Action</span>
                                        </th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="item in GroupListingData" :key="item">
                                        <td class="text-center"><input :Id="item.Id" v-bind:value="item.Id"
                                                class="chkItems" type="checkbox" v-on:change="onChange()"></td>
                                        <td>
                                            <span class="text-overflow-dynamic-container">
                                                {{ item.Name }}
                                            </span>
                                        </td>
                                        <td>
                                            <span class="text-overflow-dynamic-container">
                                                {{ item.Permission }}
                                            </span>
                                        </td>
                                        <td  v-if="refCode!='Users'">
                                            <span class="text-overflow-dynamic-container"
                                                v-if="item.SharedMembers != null">
                                                {{ item.SharedMembers }} <a href="javascript:;"
                                                    v-if="item.SharedMembers.length > 2"
                                                    @click="loadMore(item.Id)">more..</a>
                                            </span>
                                        </td>
                                        <td class="single-action text-center">
                                            <a class="actions-onclick float-none" typetitle="Edit" typefor="Edit"
                                                v-bind:value="item.Id" title="Edit"  @click="EditGroupData(item.Id)">
                                                <em class="fa fa-pencil text-success action-icon"></em> 
                                            </a>
                                            <a class="actions-onclick float-none" typetitle="Delete" typefor="Delete"
                                                v-bind:value="item.Id" title="Delete" @click="CommonDelete(item.Id)">
                                                <em class="fa fa-trash text-success action-icon"></em>
                                            </a>
                                        </td>
                                    </tr>
                                    <tr>
                                        <td v-if="NoRecordLength==0" colspan="7" class="text-danger text-center no-record">{{$t('NoRecordfound')}}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>

                </div>
            </div>
        </div>
    </div>
    <ModalShareDocument :docCode="docCode" :folderId="folderId" :GroupData="GroupData" :StorageUniqueCode="StorageUniqueCode" @BindGroupsData="BindGroupsData" :idFile="FileId" @ClosePopup="ClosePopup"
       :generateJSONData="generateJSONData" v-if="ShowModalSharePopup">
    </ModalShareDocument>
    <SharedUserList v-if="showSharedUserList"  :folderId="folderId" :RecordId="RecordId" :FileId="FileId" :refCode="refCode"></SharedUserList>
</div>
</template>
<script>
import ModalShareDocument from "../ShareDocument.vue"
import GroupListingDataService from "@/services/DataService";
import SharedUserList from "./ModelSharedMembers.vue"
export default {
    props: {
        ModalLabelForlsListing: {
            type: String,
            required: true
        },
        refCode: {
            type: String,
            require: true
        },
        FileId: {
            type: String,
            require: true
        },
        docCode:{
            type: String,
            require: true
        },
        StorageUniqueCode:
        {
            type: String,
            required: true
        },
        folderId:{
            type: String,
            required: true
        },
        generateJSONData:{
            type: Array,
            required: false
        }

    },
    components:
    {
        ModalShareDocument,
        SharedUserList
    },
    data() {
        return {
            isLoading: false,
            loggeduserid:null, //Get logged in user info
            companyId: null, // Get company id
            ShowGruopListing: false,
            ShowModalAddGroup: false,
            GroupListing: [],
            GroupListingData: [],
            GroupData: [],
            showSharedUserList: false,
            ShowModalSharePopup:false,
            GroupInfoData: [],
            NoRecordLength:null,
            UserListingShow:false
        }
    },
    created: function() {
        this.loggeduserid=this.GetUserInfo.ID;
        this.companyId=this.GetUserInfo.CompanyId;
        var vm=this;
        vm.FetchData();
    },
    methods: {

        // open list of shared users data
        loadMore: function (RecordId) {
            var vm = this;
            this.RecordId = RecordId;
            var refCode = vm.refCode;
            var FileId = vm.FileId;
            this.showSharedUserList = true;
        },
        // Common Delete method 
        CommonDelete: function (id) {
            var vm = this;
            var val = "";
            var FolderId=this.folderId
            $('#tblFileList .chkItems:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0) {
                    val += ',';
                }
                val += $(this).val();
            });
            var str = "";
            if (val.length > 0 || id != null) {
                vm.isLoading=true;
                vm.confirmR(vm.$t("ConfirmDeleteRecord"), true, (vm.$t("Delete") + "  " + vm.$t("Groups")), function () {
                    if (id > 0) {
                        str = id;
                    }
                    else {
                        str += val + ',';
                    }
                    var params = ''
                    if (vm.refCode == "Users") {
                        params = `parentIds=&sharedUserIds=${str}&sourceId=${vm.FileId}&refCode=Shared_Doc_Storage&parentFolderId=${FolderId}`;
                    }
                    else {

                        params = `parentIds=${str}&sharedUserIds=&sourceId=${vm.FileId}&refCode=Shared_Doc_Storage&parentFolderId=${FolderId}`;
                    }
                    GroupListingDataService.DeleteParentsUsersSharedFilesFoldersBySource(params).then(function (response) {
                        var box = "";
                        if (response.statusText == "OK") {
                            if (response.data == 1) {
                                $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                                $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                                $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                                box = vm.ShowAlert(vm.$t("DeletedSuccess", [vm.$t('SharedParent')]), "success", true, vm.$t("Alert"));
                                vm.FetchData();
                                
                            }
                            else if (response.data == -1) {
                                box = vm.ShowAlert(vm.$t("DeletedError", [vm.$t('SharedParent')]), "Failure", true, vm.$t("Alert"));
                                vm.isLoading=false;
                            }

                        }
                        else {

                            box = vm.ShowAlert(vm.$t("DeletedError", [vm.$t('SharedParent')]), "Failure", true, vm.$t("Alert"));
                            
                        }
                    });
                });
                vm.isLoading=false;
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
                vm.isLoading=false;
            }
        },

        // Used for close modal popups 
        ClosePopupGroups: function () {
             this.$parent.CloseGroupListingPopup();
        },
        ClosePopup: function ()
        {
            this.showSharedUserList=false;
            this.ShowModalSharePopup=false;
        },

        // Edit/fetch group data on click of edit button and passsed the data in an array to add page
        EditGroupData: function (parentid) {
            debugger
            var vm = this;        
            vm.AddEditText="Edit";
            var params= `parentid=${parentid}&CompanyId=${vm.companyId}&refCode=${this.refCode}&sourceId=${this.FileId}`;
            GroupListingDataService.GetSharedDocStorageParentUserInfo(params).then(function (response) {
                vm.GroupInfoData=JSON.parse(response.data);
                vm.GroupData=vm.GroupInfoData.DocStorageSharedUserListing;
                vm.ShowModalSharePopup = true;
            });


        },

        onChange: function () {
             var checkCount = $('.chkItems:checked').not("[id^='chkAll']").length;
             if (checkCount > 0) {
                 $('#aDeleteAll').removeClass('disabled');
             }
             else {
                 $('#aDeleteAll').addClass('disabled');
             }
         },
        //Feth data all listing data for users groups
       
        FetchData: function () {
            var vm = this;
            vm.isLoading=true;
            vm.GroupListingData =[];
            var params = `CompanyId=${this.companyId}&UserId=${this.loggeduserid}&refCode=${this.refCode}&sourceId=${this.FileId}`;
            GroupListingDataService.GetUserGroupListing(params).then(function (response) {
                vm.GroupListingData = response.data.DocStorageSharedUserListing;
                if(vm.GroupListingData==null){
                    vm.NoRecordLength=0;
                }
                vm.isLoading=false;
            })
            setTimeout(function () { vm.ResponsiveDataTable('tablelistingdata') }, 500);
        },
        OpenAddGroupPopup: function () {
            this.ShowModalSharePopup = true;

        },
        ClosePopupShared: function()
        {
            var vm=this;
            vm.ShowModalSharePopup=false;
        }
    },

}
</script>