<template>
    <div class="activity-tab">
        <div id="accordion" class="w-100">
            <div class="card"  v-for="(childItem, parentIndex) in fileTimelineData">
                <div  class="card-header" @click="ShowAccordion(parentIndex)">
                    <a :class="{'card-link':ShowHide=='collapseOne'+parentIndex,'card-link collapsed':ShowHide!='collapseOne'+parentIndex}" data-bs-toggle="collapse" v-bind:href="'#collapseOne' + parentIndex">
                        <div class="conversation-date heading-border"><span><em class="fa fa-angle-down rotate-icon pr-1" 
                                    aria-hidden="true"></em> {{ childItem.MothKey }}</span></div>
                    </a>
                </div>
                <transition name="modal" v-if="ShowHide=='collapseOne' + parentIndex">
                <div v-bind:id="'collapseOne' + parentIndex" >
                    <div class="card-body">
                        <div class="row" style="max-height:400px; overflow-y:auto;">
                            <template v-for="item in childItem.FileSummary" >

                                <div class="col-md-12 mbspc">
                                    <div class="row">
                                        <div class="col-xl-5 col-lg-5 col-md-12 col-sm-12 message-ellapse"
                                            v-bind:title="item.ActionKey">
                                            <span class="iconplus"><em class="fa fa-check-circle"></em> </span><span
                                                class="circle-oicon"><em class="fa fa-users"></em> </span>
                                            {{ item.ActionKey }}
                                        </div>
                                        <div class="col-xl-4  col-lg-4 col-md-12 col-sm-12">
                                            <div class="cont_start"><span
                                                    class="blue-time">{{ item.ActionBy }}</span>
                                                </div>
                                        </div>
                                        <div class="col-xl-3  col-lg-3 col-md-12 col-sm-12 text-right">
                                            <p>{{ item.CreatedTime }}</p>
                                        </div>
                                    </div>
                                </div>
                            </template>
                        </div>
                    </div>
                </div>
            </transition>
            </div>

            <div v-if="fileTimelineData.length==0" class="text-center text-danger p-2"> {{ $t('NoRecordfound') }} </div>
        </div>
    </div>
</template>
<script>
export default {
    props: {
       
        fileTimelineData:{
            type: Array,
            required: true
        }
    },
    data: function () {
        return {
            TimeLineData: [],
            NoRecordForTab: true,
            fgh:"",
            ShowHide:null
        }
    },
    created: function () {
        var vm = this;
        vm.gettime();
    },
    watch: {
        fileTimelineData: {
            handler(value) {
                this.TimeLineData = value;
                this.NoRecordForTab = ((this.TimeLineData != null && this.TimeLineData.length > 0) ? false : true);
            },
            deep: true
        }
    },
    methods: {
        gettime: function(){
            this.fileTimelineData.forEach((value, index) => {
                this.fgh=value.ActionDate;
            })
        },
        ShowAccordion: function(value){
            debugger
            var vm = this;
            if(vm.ShowHide!='collapseOne'+value ||  vm.ShowHide==null)
            {
                vm.ShowHide='collapseOne'+value;
            }
            else{
                vm.ShowHide='';
            }
        }
    },
}
</script>